.message{
    display: grid;
    grid-template-columns: 70px 1rem max-content;
    grid-template-rows: 6.5rem;
}

.message_ava_wrapper   {
    display: grid;
    align-items: end;

}


.message_ava_wrapper img  {
    border-radius: 50%;
    width: 4rem;
}

.message_text_wrapper{
    background-color: rgba(0,0,0,  0.3);
    display: grid;
    grid-template-columns: 20rem;
    grid-template-rows: 2rem 1fr 2rem;
    font-size: 1.2rem;
    border-radius: 1rem 1rem 1rem 0;
}

.user_name{
    color: deeppink;
    padding-left: 1rem;
    font-weight: bold;
    text-align: left;
}

.user_message{
    color: #fff;
    padding-left: 1rem;
    word-wrap: break-word;
}

.message_time{
    text-align: right;
    color: #fff;
    opacity: 0.5;
    padding-right: 1.5rem;
    padding-bottom: 0.5rem;
    text-align: right;
}

.radius{
    margin-top: 5.5rem;
    width: 1rem;
    height: 1rem;
    background: radial-gradient(circle at  0 0, rgba(0,0,0,0) 1rem, rgba(0,0,0,0.3) 0);

}
